.map {
  [class*=-balloon] {
    box-shadow: none;
  }
  [class*=-balloon__content] {
    padding: 0 !important;
    margin: 0 !important;
  }
  [class*=-balloon__layout] {
    @include shadow;
  }
  [class*=-balloon__close] {
    display: none;
  }
  [class*=-balloon__tail] {
    display: none;
  }
  &-balloon {
    width: 256px;
    padding: 8px;
    background-color: $white;
    &__img {
      width: 100%;
      height: 170px;
      &:not(:last-child) {
        margin-bottom: 8px;
      }
      img {
        @include cover;
      }
    }
    &__title {
      @include body-medium;
      @include inter;
      color: $base-900;
    }
  }
}