.tab {
  &_white {
    .tab {
      &__inner {
        background-color: $white;
      }
      &__item {
        background-color: $white;
        &:not(.tab__item_active) {
          &:hover {
            background-color: $base-200;
          }
          &:active {
            background-color: $green;
            color: $white;
            svg {
              fill: $white;
            }
          }
        }
        &_active {
          background-color: $green;
          color: $white;
          svg {
            fill: $white;
          }
        }
      }
    }
  }
  &__inner {
    display: flex;
    gap: 4px;
    padding: 4px;
    background-color: $base-100;
    border-radius: 2px;
  }
  &__item {
    flex-shrink: 0;
    height: 42px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 10px;
    padding: 0 16px;
    background-color: $base-100;
    border-radius: 2px;
    @include body;
    transition: all $transition;
    svg {
      width: 16px;
      height: 16px;
      flex-shrink: 0;
      fill: $base-900;
      transition: all $transition;
    }
    &:not(.tab__item_active) {
      &:hover {
        background-color: $base-200;
      }
      &:active {
        background-color: $green;
        color: $white;
        svg {
          fill: $white;
        }
      }
    }
    &_active {
      background-color: $green;
      color: $white;
      svg {
        fill: $white;
      }
    }
  }
  &-content {
    display: none;
    &_active {
      display: block;
    }
  }
}