.file {
  &_active {
    .file {
      &__label {
        display: none;
      }
      &__text {
        display: flex;
      }
    }
  }
  &__label {
    width: 100%;
    min-height: 49px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 8px;
    border: 1px solid $base-300;
    cursor: pointer;
  }
  &__icon {
    width: 20px;
    height: 20px;
    flex-shrink: 0;
    svg {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    @include body-medium;
    line-height: 1;
  }
  &__text {
    min-height: 49px;
    display: none;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
    padding: 8px 0;
  }
  &__name {
    margin-right: auto;
  }
  &__delete {
    width: 32px;
    height: 32px;
    flex-shrink: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $base-100;
    &:hover {
      svg {
        stroke: $base-900;
      }
    }
    svg {
      width: 14px;
      height: 14px;
      stroke: $base-700;
      stroke-width: 2;
      transition: all $transition;
    }
  }
}