.popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(#2A2A2A, .5);
  z-index: 510;
  overflow-y: auto;
  overflow-x: hidden;
  &_active {
    display: block;
  }
  &__wrap {
    min-height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    // align-items: flex-start;
    padding: $gap;
  }
  &__content {
    width: 100%;
    max-width: 532px;
    padding: 40px;
    background-color: $white;
    position: relative;
    z-index: 1;
    @media (max-width: 767px) {
      padding: 24px 16px;
    }
  }
  &__close {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 32px;
    height: 32px;
    background-color: $base-100;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    z-index: 1;
    transition: all $transition;
    &:hover {
      svg {
        stroke: $base-900;
      }
    }
    svg {
      width: 14px;
      height: 14px;
      stroke: $base-700;
      stroke-width: 2;
      transition: all $transition;
    }
  }
  &__heading {
    padding-top: 3px;
    margin-bottom: 24px;
  }
  &__title {
    @include h5;
    margin-bottom: 22px;
    @media (max-width: 767px) {
      padding-right: 40px;
    }
  }
  &__description {
    @include body;
    @media (max-width: 767px) {
      font-size: 13px;
    }
  }
  &__filter {
    margin-bottom: 20px;
    @media (max-width: 767px) {
      margin-bottom: 13px;
    }
    &-title {
      @include body;
      color: $base-700;
      margin-bottom: 8px;
    }
    .tab {
      @media (max-width: 767px) {
        display: none;
      }
      &__item {
        flex: 1;
      }
    }
    .select2-block {
      display: none;
      @media (max-width: 767px) {
        display: block;
      }
    }
  }
  .form {
    &__content {
      display: flex;
      flex-direction: column;
      gap: 13px;
    }
  }
  &__button {
    width: 100%;
  }
  &_sent {
    .popup {
      &__content {
        max-width: 491px;
      }
      &__heading {
        text-align: center;
        margin-bottom: 20px;
      }
      &__description {
        max-width: 280px;
        margin: 0 auto;
      }
    }
  }
  &_member {
    .popup {
      &__content {
        max-width: 1000px;
        padding: 0;
        @media (max-width: 1023px) {
          max-width: 500px;
        }
      }
    }
  }
  &__member {
    display: flex;
    @media (max-width: 1023px) {
      display: block;
    }
    &-photo {
      flex-basis: 400px;
      min-height: 449px;
      height: auto;
      @media (max-width: 1023px) {
        flex-basis: unset;
        width: 100%;
        min-height: auto;
        height: 400px;
      }
      img {
        @include cover;
      }
    }
    &-text {
      flex-basis: 600px;
      padding: 43px 40px 40px 16px;
      @media (max-width: 1023px) {
        flex-basis: unset;
        padding: 20px 16px 16px;
      }
    }
    &-title {
      @include h5;
      margin-bottom: 20px;
    }
    &-block {
      display: flex;
      gap: $gap;
      @media (max-width: 1023px) {
        flex-direction: column;
      }
      &:not(:last-child) {
        margin-bottom: 24px;
      }
    }
    &-subtitle {
      flex-basis: 100px;
      flex-shrink: 0;
      @include body-medium;
      color: $base-700;
      @media (max-width: 1023px) {
        flex-basis: unset;
      }
      @media (max-width: 767px) {
        font-size: 13px;
      }
    }
    &-description {
      @include body;
      @media (max-width: 767px) {
        font-size: 13px;
      }
      p {
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
  }
  &_location {
    overflow: visible;
    .popup {
      &__wrap {
        height: 100%;
        align-items: flex-start;
      }
      &__content {
        height: 100%;
        max-width: 1086px;
        padding: 24px;
        @media (max-width: 767px) {
          padding: 16px;
        }
      }
      &__close {
        width: 50px;
        height: 50px;
        top: 24px;
        right: 24px;
        @media (max-width: 767px) {
          top: 16px;
          right: 16px;
        }
      }
    }
  }
  &__location {
    height: 100%;
    display: flex;
    flex-direction: column;
    &-heading {
      flex-shrink: 0;
      margin-bottom: 32px;
      @media (max-width: 767px) {
        margin-bottom: 16px;
      }
    }
    &-hero {
      display: flex;
      gap: $gap;
      padding-right: 66px;
      margin-bottom: 32px;
      @media (max-width: 767px) {
        flex-direction: column;
        padding-right: 0;
        margin-bottom: 16px;
      }
      .tab {
        flex-basis: 312px;
        @media (max-width: 767px) {
          flex-basis: unset;
          margin-right: 66px;
        }
        &__item {
          flex: 1;
          @media (max-width: 374px) {
            padding: 0 4px;
          }
        }
      }
      .search {
        flex-grow: 1;
      }
    }
    &-subtitle {
      @include body-medium;
      color: $base-700;
      margin-bottom: 8px;
    }
    &-group {
      width: 100%;
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      gap: $gap;
      @media (max-width: 1023px) {
        grid-template-columns: repeat(2, 1fr);
      }
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
        gap: 0;
      }
      .label {
        min-height: 50px;
        align-items: center;
        padding-left: 2px;
      }
    }
    &-block {
      display: flex;
      gap: 12px;
      &:not(:last-child) {
        margin-bottom: 12px;
      }
      @media (max-width: 767px) {
        display: block;
      }
    }
    &-letter {
      padding-top: 10px;
      width: 30px;
      flex-shrink: 0;
      @include h5;
      @media (max-width: 767px) {
        padding-top: 3px;
        padding-left: 4px;
        width: auto;
      }
    }
    &-body {
      display: none;
      flex-grow: 1;
      overflow-x: hidden;
      overflow-y: auto;
      margin-bottom: 32px;
      @media (max-width: 767px) {
        margin-bottom: 16px;
      }
      &_active {
        display: block;
      }
      &::-webkit-scrollbar {
        width: 3px;
      }
      &::-webkit-scrollbar-track {
        background: $base-200;
      }
      &::-webkit-scrollbar-thumb {
        width: 100%;
        background: $green;
      }
      .popup__location-subtitle {
        margin-bottom: 12px;
      }
    }
    &-button {
      flex-basis: 295px;
      @media (max-width: 767px) {
        flex-basis: unset;
      }
      &s {
        flex-shrink: 0;
        display: flex;
        gap: $gap;
        @media (max-width: 767px) {
          flex-direction: column;
        }
      }
    }
  }
}