.cookies {
  position: fixed;
  bottom: 30px;
  left: 40px;
  padding: 20px 40px;
  background-color: $base-900;
  z-index: 15;
  @media (max-width: 1023px) {
    bottom: 0;
    left: 0;
    right: 0;
    padding: 20px 16px;
  }
  &__content {
    display: flex;
    align-items: center;
    gap: 40px;
    @media (max-width: 1023px) {
      justify-content: space-between;
      gap: $gap;
    }
    @media (max-width: 767px) {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
    }
  }
  &__description {
    @include h4;
    color: $white;
  }
  &__button {
    width: 250px;
    height: 45px;
    @media (max-width: 767px) {
      width: 100%;
    }
  }
}